.container_skills {
    display: flex;
    flex-direction: column;
}

@media (min-width: 650px) {
    .container_skills {
        display: flex;
        flex-direction: row;
        gap: 30px;
    }
}

@media (min-width: 1000px) {
    .container_skills {
        display: flex;
        gap: 200px;
    }
}

.container {
    margin: 20px 0;
}

h3 {
    font-size: 16px;
}

@media (min-width: 550px) {
    h3 {
        font-size: 20px;
    }
}

.titulo_skills{
    list-style: none;
}

.titulo_skills h3 {
    color: skyblue;
}

.container {
    width: 300px;
}

.container_itens_skills{
    margin-left: 50px;
}