.menu_operacional{
    display: none;
}

@media(min-width: 800px) {
    .menu_operacional {
        width: 600px;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 15px;
    }
}

@media(min-width: 1280px) { 
    .menu_operacional{
        width: 880px;
        gap: 30px;
    }
}

.operacional {
    text-decoration: none;
    color: #c0c0c0;
}

.operacional:hover {
    text-decoration: underline;
    color: skyblue;
}