*{
    padding: 0;
    margin: 0;
    font-size: 12px;
    color: #c0c0c0;
}

@media (min-width: 600px) { * {
    font-size: 16px
    }   
}

@media (min-width: 1280px) { * {
    font-size: 20px
    }   
}

#root {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 30px;
    background: linear-gradient(to right, #14213D, #000000);
}

.separacao {
    height: 50px;
}