
.menu_toggle {
    background-color: black;
    height: 40px;
    width: 40px;

    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;

    margin-left: 10px;
    order: -1;
}

@media (min-width: 800px){ 
    .menu_toggle {
        display: none;
    }
}

.corpo_toggle {
    background-color: skyblue;
    height: 35px;
    width: 35px;
    overflow: hidden;
    border-radius: 50%;

    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    cursor: pointer;
}

.detalhe_toggle {
    width: 15px;
    height: 3px;
    background-color: #c0c0c0;
    border: solid 2px black;
    border-radius: 5px;
    cursor: pointer;
}
