header {
    width: 100%;
    height: 50px;

    padding: 15px 0;

    display: flex;
    justify-content: flex-start;
    align-items: center;

    position: fixed;
    top: 0;

    transition: 0.7s;
}

@media(min-width: 800px) { 
    .menu {
        justify-content: center;
        padding: 25px 0;
        transition: 0.7s;
    }
}

.menu_principal {
    height: 50px;
    display: flex;
    justify-content: flex;
    align-items: center;
    gap: 20px;
    transition: 0.7s;

    position: absolute;
}

@media (min-width: 1280px) {
    .menu_principal {
        max-width: 1280px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.cabecalho_titulo {
    color: #c0c0c0;
    font-size: 24px;
}

@media (min-width: 1280px) {
    .cabecalho_titulo {
        font-size: 30px;
    }
}